import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],	//默认按照传入页面的商品类目搜索商品, 综合排序
			total: 0,
			pageNo:1,
			pageSize:5,
			sortBy:'updateDate',
			sort:'desc',
			searchKey:''
		};
	},
	
	onLoad(options){



		this.cateId = options.cateId;
		if(options.cateList)
			this.cateList = JSON.parse(unescape(options.cateList));
		else{
			this.inquiryProductCate();
		}
		//this.loadData();
		this.searchKey = options.searchKey;	//主页搜索
		this.searchBrand = options.brandId;	//从分类页点击品牌
		this.searchMerchant = options.merchantId;	//商家商品
		this.searchProduct();
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		//重新加载数据
		this.resetPage();
		this.searchProduct();
	},
	//加载更多
	onReachBottom(){
		if(this.loadingType === 'more'){
			this.pageNo = this.pageNo + 1;
			this.searchProduct()
		}
	},
	methods: {
		//查询商品类目
		inquiryProductCate() {
			this.$api.request.productCate({}, res => {
				if (res.body.status.statusCode === '0') {
					this.cateList = res.body.data.cates;
				} else {
					console.log(res.body.status.errorDesc);
				}
			},true);
		},
		//搜索商品
		searchProduct(){
			var keyArray = [];
			if(this.cateId){
				keyArray.push('PRODUCT_CATE');
			}
			if(this.searchKey){
				keyArray.push('PRODUCT_NAME');
			}
			if(this.searchBrand){
				keyArray.push('BRAND');
			}
			if(this.searchMerchant){
				keyArray.push('MERCHANT');
			}
			let searchData = {
				'keyArray': keyArray,
				'productCateUuid': this.cateId,
				'productName': this.searchKey,
				'productBrandUuid': this.searchBrand,
				'merchantUuid': this.searchMerchant,
				'startIndex': (this.pageNo-1)*this.pageSize,
				'pageSize': this.pageSize,
				'sortBy': this.sortBy,
				'sort': this.sort
			};
			//加载中
			this.loadingType = 'loading';
			this.$api.request.goodsList(searchData, res => {
				uni.stopPullDownRefresh();
				if (res.body.status.statusCode === '0') {
					var goodsList = res.body.data.products;
					this.goodsList = this.goodsList.concat(goodsList);
					this.total = res.body.data.total;
					if(this.goodsList.length>=this.total){
						this.loadingType = 'noMore';
					}else{
						this.loadingType = 'more';
					}
				} else {
					this.loadingType = 'more';
					console.log(res.body.status.errorDesc);
				}
			},true);
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add', loading) {
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			
			let goodsList = await this.$api.json('goodsList');
			if(type === 'refresh'){
				this.goodsList = [];
			}
			//筛选，测试数据直接前端筛选了
			if(this.filterIndex === 1){
				goodsList.sort((a,b)=>b.sales - a.sales)
			}
			if(this.filterIndex === 2){
				goodsList.sort((a,b)=>{
					if(this.priceOrder == 1){
						return a.price - b.price;
					}
					return b.price - a.price;
				})
			}
			
			this.goodsList = this.goodsList.concat(goodsList);
			
			//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
			this.loadingType  = this.goodsList.length > 20 ? 'nomore' : 'more';
			if(type === 'refresh'){
				if(loading == 1){
					uni.hideLoading()
				}else{
					uni.stopPullDownRefresh();
				}
			}
		},
		//重置商品及分页
		resetPage(){
			this.pageNo = 1;
			this.goodsList = [];
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			//重置商品及查询参数
			this.resetPage();
			//获取排序值及升降序
			if(this.filterIndex===0){
				this.sortBy = 'updateDate';
				this.sort = 'desc';
			}else if(this.filterIndex===1){
				this.sortBy = 'soldUnit';
				this.sort = 'desc';
			}else if(this.filterIndex===2){
				this.sortBy = 'unitPrice';
				if(this.priceOrder==1)
					this.sort = 'asc';
				else if(this.priceOrder==2)
					this.sort = 'desc';
			}
			this.searchProduct();
			
		},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			this.cateId = item.productCateUuid;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.resetPage();
			let searchData = {
				'keyArray':['PRODUCT_CATE'],
				'productCateUuid': this.cateId,
				'sortBy': 'updateDate',
				'sort': 'desc',
				'startIndex': (this.pageNo-1)*this.pageSize,
				'pageSize': this.pageSize
			};
			this.searchProduct(searchData);

		},
		//详情
		navToDetailPage(item){
			let id = item.productUuid;
			uni.navigateTo({
				url: '/pages/product/product?id='+id
			})
		},
		stopPrevent(){}
	},
}
